import React from "react";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import { useSelector } from "react-redux";

const Profile = () => {
  const currentUser = useSelector(({ user }) => user.currentUser);

  return (
    <div className="container-fluid">
      <div className="row page-title">
        <div className="col-md-12">
          <nav aria-label="breadcrumb" className="float-right mt-1">
            <ol className="breadcrumb">
              <li className="breadcrumb-item">
                <a href="/">IPolicyMart</a>
              </li>
              <li className="breadcrumb-item">
                <a href="/users-claims">Claims</a>
              </li>
            </ol>
          </nav>
          <h3 className="mb-1 mt-0">
            <b>Profile</b>
          </h3>
        </div>
      </div>
      <div style={{ height: "10px" }} />
      <div className="row">
        <div className="col-lg-10">
          <div className="verticalCenterRow">
            <AccountCircleIcon
              style={{ fontSize: "80px", marginRight: "15px" }}
            />
            <div>
              <h5>
                {currentUser.user.firstName} {currentUser.user.lastName}
              </h5>
            </div>
          </div>
          <div className="col-lg-3 col-sm-4 ">
            <div className="card card-with-shadow">
              <div className="card-body">
                <div style={{ height: "5px" }} />
                <h6>Personnal Details</h6>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div style={{ height: "40px" }} />
      <div className="row">
        <div className="col-lg-5 user-profile-input">
          <small>Your Full Name</small>
          <input
            type="text"
            className="input-cars"
            value={currentUser.user.firstName + " " + currentUser.user.lastName}
          />
        </div>
        <div className="col-lg-5 user-profile-input">
          <small>Your Mobile Number</small>
          <input
            type="text"
            className="input-cars"
            value={currentUser.user.mobileNumber}
          />
        </div>
        <div className="col-lg-5 user-profile-input">
          <small>Your Email</small>
          <input
            type="text"
            className="input-cars"
            value={currentUser.user.Email}
          />
        </div>
        <div className="col-lg-5 user-profile-input">
          <small>Your Date Of Birth</small>
          <input
            type="text"
            className="input-cars"
            value={currentUser.user.dateOfBirh}
          />
        </div>
        <div className="col-lg-5 user-profile-input">
          <small>Marital Status</small>
          <input
            type="text"
            className="input-cars"
            value={currentUser.user.martiatStatus}
          />
        </div>
      </div>
      <div className="col-4">
        <button className="btn1">Save</button>
      </div>
    </div>
  );
};

export default Profile;
